ï»¿html {
    &::-webkit-scrollbar {
        width: 10px;
    }
    &::-webkit-scrollbar-track {
        background-color: transparent;
        background-image: linear-gradient(to bottom, rgba(#fff, .2), rgba(#000, .2), rgba(#fff, .2));
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background-color: var(--main-color);
    }

    overflow-y: hidden;
    overflow-x: hidden;

    scrollbar-width: thin;
    scrollbar-color: rgba(var(--main-color), 1) rgba(var(--sec-color), 1);
    direction: rtl;

    --main-color: #FEC610;
    --sec-color: #001021;
}

body {
    font-family: "reg", sans-serif;
    color: #21262c;
    direction: rtl;
    text-align: right;

    @include Sm {
        text-align: center;
    }
}

* {
    font-family: inherit;
}

@media (min-width: 1600px){
    .container{
        max-width: 1485px;
    }
}

input,
a,
button {
    font-family: "reg", sans-serif;

    &:focus {
        outline: none;
        text-decoration: none;
    }

    &:hover {
        text-decoration: none;
    }
}

button {
    cursor: pointer;

    &:focus,
    &:active {
        outline: none;
        box-shadow: none;
    }
}

ul.res {
    padding: 0;
    margin: 0;
    list-style: none;
}
