@import "main";
// End Of Import

.loader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    @extend .Flex;
    background-color: var(--sec-color);
    z-index: 999999;
  
    .kh {
        border: 10px solid #f3f3f3;
        border-top: 10px solid var(--main-color);
        border-radius: 50%;
        width: 120px;
        height: 120px;
        animation: spin 1s linear infinite;
    }
}

.navover {
position: fixed;
width: 100%;
height: 100%;
background-color: rgba($color-sec, 0.95);
z-index: 9;
transition: 0.5s;
opacity: 0;
visibility: hidden;

&.open {
    opacity: 1;
    visibility: visible;
}
}

.sideNav {
position: fixed;
top: 0;
right: 0;
width: 100%;
height: 100%;
background-color: var(--sec-color);
z-index: 99;
overflow-y: auto;
transition: 0.5s;
@extend .Flex;
flex-direction: column;
transform-origin: right;
transform: scaleX(0);

html[dir="ltr"] & {
    transform-origin: left;
}

&.open {
    // transform-origin: left;
    transform: scaleX(1);

    ul {
    opacity: 1;

    li {
        transform: translateX(0);
        opacity: 1;
    }
    }
}

&.origin {
    transform-origin: left;

    html[dir="ltr"] & {
    transform-origin: right;
    }
}

.options {
    padding: 0 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    position: absolute;
    top: 50px;

    @include mob480 {
    padding: 0 40px;
    top: 30px;
    }

    .lang a {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    color: #fff;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    background-color: var(--main-color);
    }

    .close1 {
    img {
        filter: brightness(0) invert(1);
    }
    }
}

& > ul {
    display: flex;
    align-items: center;
    flex-direction: column;
    opacity: 0;
    transition: 0.5s;
    transition-delay: 0.5s;

    &::-webkit-scrollbar {
    width: 8px;
    }

    &::-webkit-scrollbar-track {
    background-color: var(--sec-color);
    }

    &::-webkit-scrollbar-thumb {
    // border-radius: 10px;
    background-color: var(--main-color);
    }

    @include mob480 {
    margin-top: 50px;
    }

    & > li {
    position: relative;
    padding: 7px 0;
    // margin-bottom: 20px;
    transition: 0.5s;
    transform: translateX(-10px);

    @include LTR {
        transform: translateX(10px);
    }

    opacity: 0;

    &:nth-child(1) {
        transition-delay: 0.2s;
    }

    &:nth-child(2) {
        transition-delay: 0.4s;
    }

    &:nth-child(3) {
        transition-delay: 0.6s;
    }

    &:nth-child(4) {
        transition-delay: 0.8s;
    }

    &:nth-child(5) {
        transition-delay: 1s;
    }

    &:nth-child(6) {
        transition-delay: 1.2s;
    }

    &:nth-child(7) {
        transition-delay: 1.4s;
    }

    &:nth-child(8) {
        transition-delay: 1.6s;
    }

    &:nth-child(9) {
        transition-delay: 1.8s;
    }

    &:nth-child(10) {
        transition-delay: 2s;
    }

    &:nth-child(11) {
        transition-delay: 2.2s;
    }

    a {
        width: 100%;
        display: block;
        font-size: 18px;
        color: $color-white;
        transition: 0.5s;
    }

    i {
        position: absolute;
        top: 13px;
        left: 13px;
        transform: rotate(90deg);
        font-size: 9px;
        color: currentColor;
        transition: 0.5s;

        html[dir="ltr"] & {
        left: unset;
        right: 13px;
        }

        &.open {
        transform: rotate(0deg);
        }
    }

    .drop {
        width: 100%;
        padding: 5px 15px;
        display: none;
        overflow: visible;
        height: auto;

        li {
        padding: 4px 0;

        a {
            font-size: 15px;
            color: #898989;
            transition: 0.5s;

            &:hover {
            color: #595959;
            }
        }
        }
    }
    }
}
}

// Start Navbar
section.navbar1{
    padding: 17px 0;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 9;
    transition: 0.6s cubic-bezier(0, 0.55, 0.45, 1);
    
    &.scrolled{
        padding: 10px 0;
        background-color: #fff;
        box-shadow: 0 0 15px rgba(0,0,0, .2);

        .links{

            li{

                a{
                    color: var(--sec-color) !important;
                    
                    &:hover{
                        color: var(--main-color) !important;
                    }
                }
            }
        }
    }

    .content{
        display: flex;
        align-items: center;
        justify-content: center;

        .logo{

            @include Sm{
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: space-between;
            }

            svg:not(.menuTriger){
                transform: scale(.75);
            }

            svg.menuTriger {
                display: none;
                width: 30px;
                height: 30px;
      
                path {
                  fill: var(--main-color);
                  transform: scale(1.8);
                }
      
                @include Sm {
                  display: flex;
                  align-items: center;
                  justify-content: center;
                }
              }
        }

        .links{
            display: flex;
            align-items: center;
            margin-right: auto;
            margin-left: auto;

            @include Sm{
                display: none;
            }
            

            li{

                &:not(:last-child){
                    margin-left: 35px;
                    
                    @include kh{
                        margin-left: 20px;
                    }
                }

                a{
                    font-size: 17px;
                    font-family: reg;
                    color: #fff;
                    transition: .5s;

                    &:hover{
                        color: var(--main-color);
                    }
                }
            }
        }

        .socials{
            display: flex;
            align-items: center;

            @include Sm{
                display: none;
            }

            a{
                display: flex;
                align-items: center;
                justify-content: center;
                width: 50px;
                height: 50px;
                border-radius: 50%;
                transition: 0.6s cubic-bezier(0, 0.55, 0.45, 1);

                @include kh{
                    width: 40px;
                    height: 40px;
                }

                &:hover{
                    background-color: var(--main-color) !important;
                }

                &:not(:last-child){
                    margin-left: 8px;
                }

                &:nth-child(1){
                    background-color: #5A7B56;
                }
                &:nth-child(2){
                    background-color: #C81C1C;
                }
                &:nth-child(3){
                    background-color: #668FC5;
                }
                &:nth-child(4){
                    background-color: #517DB7;
                }

                svg{
                    @include kh {
                        transform: scale(0.75);
                    }
                }
            }
        }
    }
}
// End Navbar

// Start Header

header{
    min-height: 100vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    align-items: flex-end;
    position: relative;

    > video{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: -2;
    }

    &:after{
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background-color: rgba(#001021, .4);
        z-index: -1;
    }

    .search_form{
        max-width: 1234px;
        border-radius: 40px;
        background-color: rgba(#000000, .6);
        margin-bottom: 150px;
        @include kh{
            margin-bottom: 50px;
        }
        padding: 40px 125px 45px;
        @include Sm{
            margin-bottom: 50px;
            padding: 40px;
        }
        @include mob375{
            padding: 10px;
        }
        margin-right: auto;
        margin-left: auto;

        h2{
            font-size: 20px;
            font-family: semi;
            color: var(--main-color);
            text-align: center;
            margin-bottom: 45px;

            @include mob375{
                margin-bottom: 25px;
            }
        }

        .field{
            position: relative; 
            height: 70px;
            border-radius: 100px;
            border: 1px solid #FFFFFF;

            .icon{
                position: absolute;
                top: 50%;
                right: 20px;
                transform: translateY(-50%);
                @extend .Flex;
            }

            input{
                width: 100%;
                height: 100%;
                border-radius: 100px;
                background-color: transparent;
                padding-right: 58px;
                padding-left: 30px;
                font-size: 18px;
                color: #fff;
                border: 0;

                @include mob480{
                    font-size: 14px;
                }

                &::placeholder{
                    font-size: 18px;
                    color: #fff;
                    transition: 0.6s cubic-bezier(0, 0.55, 0.45, 1);

                    @include mob480{
                        font-size: 14px;
                    }
                }

                &:focus{
                    
                    &::placeholder{
                        opacity: 0;
                        transform: translateX(-10px);
                        visibility: hidden;
                    }
                }
            }

            button{
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                left: 10px;
                width: 101px;
                height: 50px;
                background-color: var(--main-color);
                border: 0;
                border-radius: 50px;
                @extend .Flex;
                .button_icon{
                    @extend .Flex;
                    margin-left: 7px;
                }
            }
        }
    }
}

// Start Header
.tabs_head{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 60px !important;

    li{
        margin: 0 5px;
        width: 237px;
        height: 50px;
        @extend .Flex;
        border: 1px solid #BCBCBC;
        border-radius: 50px;
        cursor: pointer;
        font-size: 16px;
        font-family: med;
        color: #BCBCBC;
        
        &.active{
            font-family: semi;
            color: var(--sec-color);
            background-color: var(--main-color);
            border-color: transparent;
        }
    }
}
.head_sec{
    text-align: center;
    position: relative;
    h2{
        font-size: 35px;
        color: var(--sec-color);
        font-family: semi;
        margin-bottom: 20px;
    }

    .more{
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
        @extend .Flex;

        @include Sm{
            position: unset;
            transform: none;
            margin-top: 10px;
        }

        span{
            font-size: 18px;
            font-family: semi;
            color: var(--sec-color);
            margin-left: 12px;
            transition: .5s;
        }

        p{
            @extend .Flex;
            width: 50px;
            height: 50px;
            border-radius: 50%;
            background-color: var(--main-color);
            margin: 0;
            filter: drop-shadow(0 3px 6px rgba(0,0,0, .16))
        }

        &:hover span{
            color: var(--main-color);
        }
    }
}
// Activities

.activity{
    padding: 80px 0;

    .activity_tabs{
        margin-top: 45px;

        .tabs_body{

            .tab{
                display: none;
                &.active{
                    display: block;
                }

                .swiper-container{
                    padding-bottom: 60px;
                }
            }

            .swiper-slide{
                text-align: right;

                @include Sm{
                    text-align: center;
                }

                .pic{
                    border-radius: 40px;
                    overflow: hidden;

                    @include Sm{
                        max-width: 359px;
                        margin: auto;
                    }

                    img{
                        transition: 1s cubic-bezier(0, 0.55, 0.45, 1);
                        transform: scale(1.01);
                    }

                    &:hover{

                        img{
                            transform: scale(1.1);
                        }
                    }
                }

                .title{
                    font-size: 20px;
                    @include kh{
                        font-size: 15px;
                    }
                    margin-top: 30px;
                    margin-bottom: 20px;
                    font-family: semi;
                    color: var(--sec-color);
                    line-height: 30px;
                }

                .details{
                    @extend .Flex;
                    justify-content: flex-start;
                    margin-bottom: 25px;

                    @include Sm{
                        justify-content: center;
                    }

                    p{
                        display: flex;
                        align-items: center;

                        &:first-child{
                            margin-left: 10px;
                        }

                        svg{
                            margin-left: 10px;
                        }

                        span{
                            font-size: 14px;
                            @include kh{
                                font-size: 11px;
                            }
                            color: #BCBCBC;
                        }
                    }
                }

                .go_more{
                    display: flex;
                    align-items: center;

                    @include Sm{
                        justify-content: center;
                    }

                    svg{
                        @include kh{
                            transform: scale(.75);
                        }
                    }

                    span{
                        margin-right: 10px;
                        font-size: 18px;
                        @include kh{
                            font-size: 14px;
                        }
                        font-family: semi;
                        color: var(--main-color);
                        text-decoration: underline;
                    }
                }
            }

            .swiper-pagination{
                bottom: -12px;
                height: 30px;
                span{
                    width: 9px;
                    height: 9px;
                    transform: none;
                    background-color: rgba(0,0,0, .2);
                    opacity: 1;
                    @extend .trans;

                    &.swiper-pagination-bullet-active{
                        transform: scale(1.34);
                        background-color: var(--sec-color);
                    }
                }
            }
        }
    }

    &.black_sec{
        background-image: url("../images/books-back.jpg");
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;

        .head_sec{

            h2, .more span{
                color: #FFF;
            }

            .more span:hover{
                color: var(--main-color);
            }
        }

        .tabs_body .swiper-slide .pic img {
            transform: scale(1.01);
        }

        .swiper-slide{

            .pic{
                position: relative;

                .download_sign{
                    position: absolute;
                    top: 17px;
                    left: 20px;
                    z-index: 5;
                }
            }

            .title{
                color: #fff !important;
            }
        }

        .swiper-pagination{
            span{
                background-color: rgba(255,255,255, .2) !important;

                &.swiper-pagination-bullet-active{
                    background-color: var(--main-color) !important;
                }
            }
        }
    }

    &.inner{

        .small_video{
            position: relative;
            background-color: #001021;
            display: block;
            overflow: hidden;
            border-radius: 40px;

            @include Sm{
                max-width: 359px;
                margin: auto;
                margin-top: 35px;
            }

            &:after{
                content: "";
                width: 100%;
                height: 100%;
                display: block;
                position: absolute;
                top: 0;
                left: 0;
                background-image: linear-gradient(to bottom, transparent, #001021);
                z-index: 1;
            }

            .details{
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 100%;
                @extend .Flex;
                flex-direction: column;
                z-index: 2;

                .play_btn{

                    @include kh{
                        transform: scale(.75);
                    }
                }

                .title{
                    margin: 30px 0;
                    font-size: 20px;
                    font-family: semi;
                    color: #FFF;

                    @include kh{
                        margin: 20px 0px;
                        font-size: 14px;
                    }
                }

                .bottom_details{
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    span{
                        height: 45px;
                        border: 1px solid #fff;
                        border-radius: 50px;
                        @extend .Flex;
                        padding: 12px 17px;
                        margin-bottom: 0;
                        margin-top: 3px;
                        font-size: 16px;
                        color: #fff;

                        @include kh{
                            height: 40px;
                            font-size: 12px;
                        }
    
                        &:last-child{
                            background-color: #fff;    
                            font-size: 18px;
                            font-family: semi;
                            color: var(--sec-color);
                            margin-right: 8px;

                            @include kh{
                                font-size: 14px !important;
                            }
                        }
                    }
                }
            }
        }
    }
}

