    background-size: cover;
    background-repeat: no-repeat;

    .content{
        display: flex;
        align-items: center;
        justify-content: center;

        @include Sm{
            flex-direction: column;
        }

        .right{
            display: flex;
            align-items: center;

            @include Sm{
                flex-direction: column;
                justify-content: center
            }

            .details{
                max-width: 240px;
                margin-right: 20px;

                @include Sm{
                    margin-right: 0;
                    margin-top: 10px;
                    max-width: 100%;
                }
                h3{
                    font-size: 25px;
                    font-family: semi;
                    color: var(--sec-color);
                    margin-bottom: 15px;
                }

                p{
                    margin-bottom: 0;
                    line-height: 20px;
                    font-size: 16px;
                    color: #fff;
                }
            }
        }

        .left{
            margin-right: 42px;

            @include Sm{
                margin-top: 20px;
                margin-right: 0;
                width: 100%;
            }

            form{
                width: 592px;
                max-width: 592px;
                height: 70px;
                position: relative;

                @include Sm{
                    width: 100%;
                    max-width: 100%;
                }
    
                input{
                    width: 100%;
                    height: 100%;
                    border: 0;
                    border-radius: 100px;
