.Flex {
    display: flex;
    align-items: center;
    justify-content: center;
}

.trans{
    transition: 0.6s cubic-bezier(0, 0.55, 0.45, 1);
}

@keyframes pulse {
  0% {
    opacity: 1;
  }

  50% {
    opacity: 0.6;
  }

  100% {
    opacity: 1;
  }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}